import Vue from 'vue'
import VueRouter from 'vue-router'
import firebase from 'firebase';

/*

import Home from '../views/Home.vue'

*/

import Login from '../views/Login.vue'
import Kuyular from '../views/Kuyular.vue'
import KuyuDetay from '../views/KuyuDetay.vue'
import NotFound from '../views/NotFound.vue'

Vue.use(VueRouter)

const routes = [
  /*

    {
    path: '/',
    name: 'Home',
    component: Home
  },

  */
 
  {
    path: '/',
    name: 'Login',
    component: Login

  },
      
  
  {
    
   path: '/kuyular',
    name: 'Kuyular',
    component: Kuyular,
    meta:{
      authRequired:true,
    }
  },
  {
  
    path: '/kuyudetay/:hizmet_no/:kuyu_adi/:vesile/:kuyu_sahibi/:tel/:kaydeden_kullanici/:kuyu_yeri/:kayit_tarihi/:grubu/:gorsel_linki/:kuyu_durumu',    
   name: 'KuyuDetay',
    component: KuyuDetay,
    props:true 
  },


  {
    path: '/:catchAll(.*)',
    name: 'NotFound',
    component: NotFound
  },


  
]



 const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
  

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.authRequired)) {
      if (firebase.auth().currentUser) {
          next();
      } else {
      //    alert('Giriş Yapmanız Gerekmektedir');
          next({
              path: '/',
          });
      }
  } else {
      next();
  }
});


export default router


